.works {
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  z-index: 4;
  background-color: inherit;
  color: inherit;
  position: relative;
  border-radius: 64px 64px 0px 0px;
  padding: 40px 16px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px -42px 60px rgba(0, 0, 0, 0.1), 0px -300px 250px rgba(0, 0, 0, 0.08);
  -webkit-animation: infinitiTransform 99999s infinite forwards;
          animation: infinitiTransform 99999s infinite forwards;
}
.works__title {
  display: block;
  padding-bottom: 40px;
  text-align: center;
  font-size: 40px;
  line-height: 48px;
  font-family: "Inter-SemiBold";
}
.works__container {
  margin: auto;
}
.works__container .works__list {
  display: grid;
  list-style: none;
  grid-template-columns: repeat(2, 407px);
  gap: 32px;
}

@media screen and (max-width: 900px) {
  .works {
    padding: 40px 6px;
  }
  .works__container {
    max-width: 100%;
  }
  .works__container .works__list {
    grid-template-columns: repeat(1, 407px);
    max-width: 100%;
  }
}
@media screen and (max-width: 600px) {
  .works {
    border-radius: 32px 32px 0px 0px;
  }
  .works .works__title {
    font-size: 32px;
    line-height: 39px;
  }
  .works__container .works__list {
    grid-template-columns: repeat(1, 338px);
  }
  .works .works__item_soon .works__item-comming-soon {
    font-size: 18px;
    line-height: 22px;
  }
}
@media screen and (max-width: 412px) {
  .works .works__title {
    padding-bottom: 32px;
    font-size: 32px;
    line-height: 39px;
  }
  .works__container {
    max-width: 100%;
  }
  .works__container .works__list {
    grid-template-columns: repeat(1, 95vw);
    max-width: 100%;
  }
}
@-webkit-keyframes infinitiTransform {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(1px);
  }
  100% {
    transform: translateY(-1px);
  }
}
@keyframes infinitiTransform {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(1px);
  }
  100% {
    transform: translateY(-1px);
  }
}/*# sourceMappingURL=works.css.map */